import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl,FormBuilder,Validators, NgForm } from '@angular/forms';
import {AuthenticateService} from '../../services/authenticate.service';
import{Router} from '@angular/router';

import{ HttpClientModule}from '@angular/common/http';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers:[AuthenticateService]
})
export class RegisterComponent implements OnInit {
  registerForm:FormGroup;
  name:String;
  email:String;
  username:String;
  password:String;
  confirmpassword:String;
  private readonly baseurl="http://localhost:3000/users/register";
  constructor(private fb: FormBuilder,private authService:AuthenticateService,private router:Router,private http:HttpClientModule) { }

  ngOnInit() {
    this.registerForm = this.fb.group({
      name: ['', [Validators.required,Validators.pattern("/^[a-zA-Z]*$/")]],
      email: ['', [Validators.required, Validators.pattern("/^\w+([\.-]?\w+)+@\w+([\.:]?\w+)+(\.[a-zA-Z0-9]{2,3})+$/")]],
      username: ['', [Validators.required,Validators.pattern("/^[a-zA-Z]*$/")]],
      password: ['', [Validators.required, Validators.minLength(6)]],
      confirmPassword: ['', [Validators.required,Validators.minLength(6)]]
    });
    
  }
 get f() { return this.registerForm.controls; }

 onregisterSubmit(form:NgForm)
 {
  this.authService.registerUser(form.value).subscribe(next => {
    
    alert("successfully registered");
    this.router.navigate(['/login']);
  },
  error => {
    console.log("failed submitting");
  });
   

 }
}
