import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl,FormBuilder,Validators, NgForm } from '@angular/forms';

import {AuthenticateService} from '../../services/authenticate.service';
import{Router} from '@angular/router';

import{ HttpClientModule}from '@angular/common/http';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm:FormGroup;
   
  constructor(private fb:FormBuilder,private authService:AuthenticateService,private router:Router,private http:HttpClientModule) { }

  ngOnInit() {
    this.loginForm = this.fb.group({
      
      username: ['', [Validators.required,Validators.pattern("/^[a-zA-Z]*$/")]],
      password: ['', [Validators.required, Validators.minLength(6)]],
      
    });
  }
  onloginSubmit(form:NgForm)
  {
   this.authService.authenticateUser(form.value).subscribe(data => {
    
     alert(JSON.stringify(data));
     console.log(data);
   },
   error => { 
     console.log("failed submitting");
   });
    
 
  }

}
